<template>
  <header class="header shadow">
    <h1 class="header__title">{{ title }}</h1>
    <c-avatar class="header__avatar" :src="getAccount.avatar" size="30" radius="50%" />
  </header>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'l-header-mobile',

  components: {
    cAvatar: () => import('@/components/Avatar')
  },

  data: () => ({
    title: ''
  }),

  computed: {
    ...mapGetters({ getAccount: 'account/get' })
  },

  watch: {
    $route: {
      immediate: true,
      handler (to) {
        const view = to.matched[0].components.default

        if (view.metaInfo && view.metaInfo.title) {
          this.title = view.metaInfo.title
        }
      }
    }
  }
}
</script>

<style scoped>
  .header {
    background: #fff;
    padding: 15px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header__title {
    color: hsl(var(--ui-primary));
    font-size: 20px;
    font-weight: 500;
    margin: 0;
    flex: 1;
    margin-right: 15px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
</style>